const claimApply=[
	{
		path:'/claimapply',
		name:'ClaimApply',
		redirect:'/claimapply/apply',
		component: () => import('@/components/pages'),
		children:[
			{
				path: 'apply',
		   		name: 'Apply',
		   		meta:{
		   			keepAlive:true,
		   		},
		   		component: () => import('@/components/pages/claimApply/Apply')
			},
			{
				path: 'apply/recordreport',
		   		name: 'RecordReport',
		   		meta:{
		   			keepAlive:true,
		   		},
		   		component: () => import('@/components/pages/claimApply/RecordReport')
			},
			{
				path: 'apply/recordreport/insurepeople',
		   		name: 'InsurePeople',
		   		meta:{
		   			keepAlive:false,
		   		},
		   		component: () => import('@/components/pages/claimApply/InsurePeople')
			},
			{
				path: 'apply/recordreport/insurepeople/applicantpeople',
		   		name: 'ApplicantPeople',
		   		meta:{
		   			keepAlive:false,
		   		},
		   		component: () => import('@/components/pages/claimApply/ApplicantPeople')
			},
			{
				path: 'apply/recordreport/insurepeople/applicantpeople/uploading',
		   		name: 'Uploading',
		   		meta:{
		   			keepAlive:false,
		   		},
		   		component: () => import('@/components/pages/claimApply/Uploading')
			},
			{
				path: 'apply/recordreport/insurepeople/applicantpeople/uploading/confirmsub',
		   		name: 'ConfirmSub',
		   		meta:{
		   			keepAlive:false,
		   		},
		   		component: () => import('@/components/pages/claimApply/ConfirmSub')
			},
			{
				path: 'apply/recordreport/insurepeople/applicantpeople/uploading/confirmsub/subinformation',
		   		name: 'SubInformation',
		   		meta:{
		   			keepAlive:false,
		   		},
		   		component: () => import('@/components/pages/claimApply/SubInformation')
			},
			{
				path: 'apply/jssdk',
		   		name: 'JSSDK',
		   		meta:{
		   			keepAlive:false,
		   		},
		   		component: () => import('@/components/pages/claimApply/components/Jssdk')
			}
		]
	}
	
];
export default claimApply; 


