const claimSearch=[
	{
		path:'/claimsearch',
		name:'ClaimSearch',
		redirect:'/claimsearch/suppinformation',
		component: () => import('@/components/pages'),
		children:[
			{
				path: 'suppinformation',
		   		name: 'SuppInformation',
		   		component: () => import('@/components/pages/claimSearch/SuppInformation')
			}
		]
	}
];
export default claimSearch; 


